#profile {
    display: flex;
    justify-content: center;
    gap: 5rem;
    height: 50vh;
}

.section__pic-container {
    display: flex;
    height: 400px;
    width: 400px;
    margin: auto 0;
}

.section__text {
    align-self: center;
    text-align: center;
}

.section__text p {
    font-weight: 600;
}

.section__text__p1 {
    text-align: center;
}

.section__text__p2 {
    font-size: 1.75rem;
    margin-bottom: 1rem;
}

.title {
    font-size: 3rem;
    text-align: center;
    width: 350px; 
    overflow: hidden;
    white-space: nowrap;
    margin: 0 auto; 
}

#socials-container {
    display: flex;
    justify-content: center;
    margin-top: 1rem;
    gap: 1rem;
}

.icon {
    cursor: pointer;
    height: 2rem;
}

.btn-container {
    display: flex;
    justify-content: center;
    gap: 1rem;
}

.btn {
    font-weight: 600;
    transition: all 300ms ease;
    padding: 1rem;
    width: 8rem;
    border-radius: 2rem;
}

.btn-color-1,
.btn-color-2 {
    border: rgb(53, 53, 53) 0.1rem solid;
}

.btn-color-1:hover,
.btn-color-2:hover {
    cursor: pointer;
}

.btn-color-1,
.btn-color-2:hover {
    background: rgb(53, 53, 53);
    color: white;
}

.btn-color-1:hover {
    background: rgb(0, 0, 0);
}

.btn-color-2 {
    background: none;
}

.btn-color-2:hover {
    border: rgb(255, 255, 255) 0.1rem solid;
}

.btn-container {
    gap: 1rem;
}

#about {
    position: relative;
    height: 80vh;
    margin-bottom: 10vh;
}
  
.about-containers {
    gap: 2rem;
    margin-bottom: 2rem;
    margin-top: 2rem;
}
  
.about-details-container {
    justify-content: center;
    flex-direction: column;
}
  
.about-containers {
    display: flex;
}
    
.details-container {
    padding: 1.5rem;
    flex: 1;
    background: white;
    border-radius: 2rem;
    border: rgb(53, 53, 53) 0.1rem solid;
    border-color: rgb(163, 163, 163);
    text-align: center;
}

.default-cursor:hover {
    cursor: default;
}
  
.section-container {
    gap: 4rem;
    height: 80%;
}

@media screen and (min-width:1200px){
    #about{
        width: 65%; 
        margin-left: auto;
        margin-right: auto;
    }
}

/* Optimize for mobile screen */
@media screen and (max-width: 1200px) {
    #profile {
        height: 80vh;
        margin-bottom: 5rem;
    }
}

@media screen and (max-width: 900px) {
    #profile,
    .section-container {
        display: block;
    }
    section,
    .section-container {
        height: fit-content;
    }
    section {
        margin: 0 5%;
    }
    .section__pic-container {
        width: 275px;
        height: 275px;
        margin: 0 auto 2rem;
    }
}

@media screen and (max-width: 600px) {
    #profile {
        margin-bottom: 2rem;
    }
    .title {
        width: 100%;
        font-size: 2rem; 
    }
    .section__text__p2 {
        font-size: 1.25rem;
    }
}